import { useState } from "react"
import Highlighter from "react-highlight-words"
import { RPTooltip } from "@rp/react/web/common/base-components"
import {
  EllipsisTooltip,
  GroupTitleStyled,
  ShowMoreButton,
} from "@rp/react/web/common/composite-components"
import { IconEntity } from "@rp/react/web/icons"
import { IconButton, Typography, styled, useTheme } from "@rp/react/web/mui/core"
import { Close } from "@rp/react/web/mui/icons"
import { PanelGroup } from "."

const RowContainer = styled("div", { name: "RowContainer" })(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "space-between",
  height: "26px",
  borderRadius: theme.spacing(0.5),
  paddingLeft: theme.spacing(0.5),
  "@media (hover : hover)": {
    button: {
      display: "none",
    },
    "&:hover": {
      backgroundColor: theme.palette.overlay.surface,
      "& button": {
        display: "flex",
      },
    },
  },
}))

const WatchlistsPanelGroup = ({
  group,
  value,
  onDelete,
  filterTerm,
}: {
  group: string
  value: PanelGroup
  onDelete: (id: string) => void
  filterTerm: string
}) => {
  const [showMore, setShowMore] = useState(false)
  const theme = useTheme()
  return (
    <div>
      <GroupTitleStyled sx={{ justifyContent: "space-between" }}>
        <IconEntity icon={value.icon} />
        <Typography variant="subtitle1" fontWeight="bold" sx={{ marginRight: "auto" }}>
          {group}
        </Typography>
        <Typography variant="h6" fontSize={12} color="text.secondary">
          {value.items.length}
        </Typography>
      </GroupTitleStyled>

      {value.items
        .slice(0, showMore ? value.items.length : 5)
        .map(({ name, key, metadata1, category }) => (
          <RowContainer key={key}>
            <EllipsisTooltip tooltipText={name}>
              <Highlighter
                autoEscape
                highlightStyle={{ color: theme.palette.primary.light }}
                highlightTag="span"
                searchWords={[filterTerm]}
                textToHighlight={name}
              />
            </EllipsisTooltip>

            <Typography fontSize={12} sx={{ color: "#A4D9FF", m: "4px auto 0 4px" }}>
              <Highlighter
                autoEscape
                highlightStyle={{ color: theme.palette.primary.light }}
                highlightTag="span"
                searchWords={[filterTerm]}
                textToHighlight={
                  (category === "Companies" || category === "ETFs") && metadata1 !== undefined
                    ? metadata1
                    : ""
                }
              />
            </Typography>
            <RPTooltip title="Remove from Watchlist" placement="bottom-start">
              <div>
                <IconButton size="small" onClick={() => onDelete(key)}>
                  <Close sx={{ fontSize: "16px", marginRight: "auto" }} />
                </IconButton>
              </div>
            </RPTooltip>
          </RowContainer>
        ))}
      {value.items.length > 5 && (
        <ShowMoreButton
          sx={{ margin: "0", ml: 0.5 }}
          showMore={showMore}
          onClick={() => setShowMore(!showMore)}
          qty={value.items.length - 5}
        />
      )}
    </div>
  )
}

export { WatchlistsPanelGroup }
